    public setDefaultLang(lang: string) {
        localStorage.lang = lang;
    }

    public getDefaultLang(): string {
        if (localStorage.lang == null) {
            return 'en';
        } else {
            return localStorage.lang;
        }
    }

    public flattenMarket(market: any[]) {
        if (!market || market.length === 0) {
            return [];
        }

        market.forEach(item => {
            market.push(this.flattenMarket(item['children']));
            item['children'] = [];
        });
        return market;
    }

    public handleHttpError(error: HttpErrorResponse | ErrorResponse | any): Observable<any> {
        // In a real world app, we might use a remote logging infrastructure

        console.error(error);

        let instance = this;

        let errorResponse: ErrorResponse;
        if (error instanceof ErrorResponse) {
            errorResponse = error;
        }
        if (error instanceof HttpErrorResponse) {

            if (error.status === 0) {

                errorResponse = new ErrorResponse();
                errorResponse.errors = [new ErrorResponseNode('ERR_CONNECTION_ERROR', 'ERR_CONNECTION_ERROR', null)];

            } else if (error.status === 404) {

                errorResponse = new ErrorResponse();
                errorResponse.errors = [new ErrorResponseNode('ERR_RESOURCE_NOT_FOUND', 'ERR_RESOURCE_NOT_FOUND', null)];

            } else {

                let body = error.error;
                if (body != null && body.errors != null) {
                    errorResponse = <ErrorResponse>body;
                }
            }
        }

        if (errorResponse == null) {
            errorResponse = new ErrorResponse();
            errorResponse.errors = [new ErrorResponseNode('ERR_SYS_UNKNOWN', 'ERR_SYS_UNKNOWN', null)];
        }

        if (errorResponse.errors[0].errorCode === 'ERR_SYS_EXPIRED_TOKEN') {
            this.handleTokenExpired();
        }

        return observableThrowError(errorResponse);

    }

