import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MemberInfoRegisterComponent } from './core/components/member-info-register';


const routes: Routes = [
  //tempUmhBookingId : "c3bbbea8-b153-4237-8126-a817b54d64a4",
  //tempUmhBookingId : "0c3f28eb-ddce-4fd8-9a6f-5b34abc56126"
  {
    path:'',
    redirectTo: '/c3bbbea8-b153-4237-8126-a817b54d64a4',
    pathMatch: 'full',
  },

  {
    path: ':bookingId',
    component: MemberInfoRegisterComponent,
  },

  
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'enabled', initialNavigation: 'enabled' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
