import { HttpInterceptor, HttpRequest, HttpEvent, HttpHandler, HttpParams, HttpParameterCodec } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";

@Injectable()
export class EncodeHttpParamsInterceptor implements HttpInterceptor {
    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

        let params = new HttpParams({ encoder: new CustomEncoder(), fromString: request.params.toString() });
        let body = request.body;
        if (request.body instanceof HttpParams) {
            body = new HttpParams({ encoder: new CustomEncoder(), fromString: request.body.toString() });
            // request = request.clone({ params: body })
        }
        request = request.clone({ params: params, body: body })
        return next.handle(request);
    }
}


class CustomEncoder implements HttpParameterCodec {
    encodeKey(key: string): string {
        return encodeURIComponent(key);
    }

    encodeValue(value: string): string {
        return encodeURIComponent(value);
    }

    decodeKey(key: string): string {
        return decodeURIComponent(key);
    }

    decodeValue(value: string): string {
        return decodeURIComponent(value);
    }
}