
import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { RouterModule } from '@angular/router';
import { NgbModule, NgbAlertModule, NgbProgressbarModule } from '@ng-bootstrap/ng-bootstrap';
import { HeaderComponent } from './components/header/header.component';
import { CustomFormsModule } from 'ngx-custom-validators';
import { NgxDropzoneModule } from 'ngx-dropzone';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatProgressBarModule } from '@angular/material/progress-bar';
import { MatDialogModule } from '@angular/material/dialog';
import { MatButtonModule } from '@angular/material/button';
import { FlexLayoutModule } from '@angular/flex-layout';
import { MatIconModule } from '@angular/material/icon';
import { MatListModule } from '@angular/material/list';
import { MatMenuModule } from '@angular/material/menu';
import { MatBadgeModule } from '@angular/material/badge';
import { MatTabsModule } from '@angular/material/tabs';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatTableModule } from '@angular/material/table';
import { MatSelectModule } from '@angular/material/select';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import { OwlDateTimeModule, OWL_DATE_TIME_FORMATS } from 'ng-pick-datetime';
import { OwlMomentDateTimeModule, OWL_MOMENT_DATE_TIME_ADAPTER_OPTIONS } from 'ng-pick-datetime-moment';
import { PerfectScrollbarModule, PERFECT_SCROLLBAR_CONFIG, PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import { NgxSpinnerModule } from "ngx-spinner";
import { NgxPermissionsModule } from 'ngx-permissions';
import { LocaleSorterPipe } from './pipes/locale-sorter.pipe';
import { LocaleFilterPipe } from './pipes/locale-filter.pipe';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ChartsModule } from 'angular-bootstrap-md';
import { NgSelectModule } from '@ng-select/ng-select';
import { MatInputModule } from '@angular/material/input';
import { MatRadioModule } from '@angular/material/radio';
import { MatNativeDateModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatCardModule } from '@angular/material/card';
import { MatPaginatorModule, MatPaginatorIntl } from '@angular/material/paginator';
import { MatSortModule } from '@angular/material/sort';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { FooterComponent } from './components/footer/footer.component';
import { LoadingDialog } from './components/loading-dialog/loading-dialog';
import { ErrorInterceptor } from './components/interceptor/error.interceptor';
import { EncodeHttpParamsInterceptor } from './components/interceptor/encode-http-params-interceptor';
import { I18nMatPaginatorIntl } from './utils/i18n-mat-paginator-intl';
import { TextMaskModule } from 'angular2-text-mask';
import { NgxMaskModule, IConfig } from 'ngx-mask'
import { AmountConverterPipe } from './pipes/amount-converter.pipe';
import { CurrencyConverterPipe } from './pipes/currency-converter.pipe';
import { TncDialog } from './components/tnc-dialog/tnc-dialog';
import { MemberInfoRegisterComponent } from './components/member-info-register'
import { MatFormFieldModule } from '@angular/material/form-field'


export const MY_MOMENT_FORMATS = {
	fullPickerInput: 'YYYY-MM-DD HH:mm',
	datePickerInput: 'YYYY-MM-DD',
	monthYearLabel: 'MMM YYYY',
	timePickerInput: 'HH:mm',
};

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
	// wheelPropagation: true
};

export const options: Partial<IConfig> | (() => Partial<IConfig>) = {};


@NgModule({
	imports: [
		CommonModule,
		FormsModule,
		RouterModule,
		TranslateModule,
		MatTooltipModule,
		NgxDropzoneModule,
		NgxPermissionsModule,
		MatProgressBarModule,
		MatDialogModule,
		MatButtonModule,
		MatProgressSpinnerModule,
		MatIconModule,
		MatListModule,
		MatMenuModule,
		MatBadgeModule,
		FlexLayoutModule,
		NgSelectModule,
		TextMaskModule,
		NgxMaskModule.forRoot(options),
		NgbModule,
		NgxSpinnerModule,
		InfiniteScrollModule,
		CustomFormsModule,
    MatFormFieldModule,
    ReactiveFormsModule,
    MatAutocompleteModule,
    MatSelectModule,
    MatRadioModule,
    MatCheckboxModule,
	],
	declarations: [
		LocaleFilterPipe,
		AmountConverterPipe,
		CurrencyConverterPipe,
		LocaleSorterPipe,
		LoadingDialog,
		HeaderComponent,
		FooterComponent,
		TncDialog,
    MemberInfoRegisterComponent,
	],
	exports: [
		CommonModule,
		HttpClientModule,
		FormsModule,
		ReactiveFormsModule,
		TranslateModule,
		OwlMomentDateTimeModule,
		PerfectScrollbarModule,
		ChartsModule,
		FlexLayoutModule,
		NgSelectModule,
		MatButtonModule,
		MatMenuModule,
		MatSelectModule,
		MatInputModule,
		MatTableModule,
		MatAutocompleteModule,
		MatRadioModule,
		MatIconModule,
		MatNativeDateModule,
		MatProgressBarModule,
		MatDatepickerModule,
		MatCardModule,
		MatPaginatorModule,
		MatSortModule,
		MatCheckboxModule,
		MatProgressSpinnerModule,
		MatSnackBarModule,
		MatTabsModule,
		MatTooltipModule,
		MatDividerModule,
		MatExpansionModule,
		MatListModule,
		MatButtonToggleModule,
		NgxDropzoneModule,
		NgxPermissionsModule,
		NgbProgressbarModule,
		NgbAlertModule,
		NgbModule,
		TextMaskModule,
		NgxMaskModule,
		NgxSpinnerModule,
		CustomFormsModule,
		LocaleFilterPipe,
		AmountConverterPipe,
		CurrencyConverterPipe,
		LocaleSorterPipe,
		NgxPermissionsModule,
		InfiniteScrollModule,
		HeaderComponent,
		FooterComponent,
		TncDialog,
    MemberInfoRegisterComponent,
	],
	providers: []
})
export class CoreModule {
	static forRoot(): ModuleWithProviders {
		return {
			ngModule: CoreModule,
			providers: [
				{ provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
				{ provide: HTTP_INTERCEPTORS, useClass: EncodeHttpParamsInterceptor, multi: true },
				{ provide: OWL_MOMENT_DATE_TIME_ADAPTER_OPTIONS, useValue: { useUtc: true } },
				{ provide: OWL_DATE_TIME_FORMATS, useValue: MY_MOMENT_FORMATS },
				{
					provide: PERFECT_SCROLLBAR_CONFIG,
					useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG,
				},
				{
					provide: MatPaginatorIntl, deps: [TranslateService],
					useFactory: (translateService: TranslateService) => new I18nMatPaginatorIntl(translateService)
				}
			]
		};
	}
}
