import { Pipe, PipeTransform } from '@angular/core';


@Pipe( { name: 'currencyConverter' } )
export class CurrencyConverterPipe<T> implements PipeTransform {
    transform(currency: string ): string {
        if(currency != null) {
            if(currency === 'HKD') return 'HK';            
        }
        return currency;
    }
}