import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { Router } from '@angular/router';
import swal from 'sweetalert2'
import { TranslateService } from '@ngx-translate/core';
import { SessionService } from '@app/core/services/front-end-service/session.service';

@Injectable()
export class ErrorInterceptor implements HttpInterceptor {

    constructor(
        private router: Router,
        private translate: TranslateService,
        private sessionService: SessionService,
    ) { }

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

        let instance = this;

        return next.handle(request).pipe(tap(() => { },
            (err: any) => {
                if (err instanceof HttpErrorResponse) {
                    if (err.error &&
                        (
                            err.error.errors && err.error.errors[0] && err.error.errors[0].errorCode == "ERR_SYS_EXPIRED_TOKEN"
                            || err.error.errors && err.error.errors[0] && err.error.errors[0].errorCode == "ERR_FORCE_CHANGE_PASSWORD"
                        )
                    ) {
                        instance.router.navigate(['/auth/login']);
                        swal.fire({
                            icon: 'error',
                            //    title: 'Fail to add a new presenter',
                            text: this.translate.instant("ERROR.ERR_SYS_EXPIRED_TOKEN"),
                            // confirmButtonText: texts.btn,
                            allowOutsideClick: false,
                            heightAuto: false,
                        }).then(function () {
                            instance.sessionService.clearSession();
                        });
                    } else if(err.error.errors && err.error.errors[0] && err.error.errors[0].errorCode == 'ERR_ACCESS_DENIED') {
                        instance.router.navigate(['/dashboard']);
                        swal.fire({
                            icon: 'error',
                            //    title: 'Fail to add a new presenter',
                            text: this.translate.instant("ERROR.ERR_ACCESS_DENIED"),
                            // confirmButtonText: texts.btn,
                            allowOutsideClick: false,
                            heightAuto: false,
                        })
                    }                    
                    else if (err.status == 401) {
                        instance.router.navigate(['/auth/login']);
                    }
                    // this.router.navigate(['login']);
                }
            }));

    }

}