import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '@env/environment';
import { catchError } from 'rxjs/operators';
import { CommonService } from './common.service';

@Injectable({
  providedIn: 'root'
})
export class MemberService {

  constructor(
    private httpClient: HttpClient,
    private commonService: CommonService,
  ) { }

  
  getBookingMemberInfo(umhMemberId: string){
    return this.httpClient.get(`${environment.API_END_POINT}/umh_members/${umhMemberId}/pre-registration`,
    {
      params:{
        umhBookingId: umhMemberId
      }
    }
    ).pipe(
        catchError((error: Response | any) => this.commonService.handleHttpError(error))
    );
  }

  putMemberInfo(memberId : number, memberInfo){
    return this.httpClient.put(`${environment.API_END_POINT}/umh_members/${memberId}/pre-registration`,memberInfo,
      {
        headers: new HttpHeaders({
          'Content-Type': 'application/json',
        }),

        params:{
          memberId: memberId.toString()
        }

      }
    )
  }


  getCounties(){
    return this.httpClient.get(`${environment.API_END_POINT}/counties`).pipe(
      catchError((error: Response | any) => this.commonService.handleHttpError(error))
    );
  }


  getDistricts(){
    return this.httpClient.get(`${environment.API_END_POINT}/districts`).pipe(
      catchError((error: Response | any) => this.commonService.handleHttpError(error))
    );
  }


  getRegions(){
    return this.httpClient.get(`${environment.API_END_POINT}/regions`).pipe(
      catchError((error: Response | any) => this.commonService.handleHttpError(error))
    );
  }


  

}
