import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { NavItem } from '@app/core/models/front-end/nav-item';
import { LoadingDialog } from '@app/core/components/loading-dialog/loading-dialog';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';

@Injectable({
    providedIn: 'root'
  })
  export class LayoutService {
  
    menuSelected$ = new BehaviorSubject<NavItem>(null);
    sideNavOpened$ = new BehaviorSubject<boolean>(false);
    subHeaderTitle$ = new BehaviorSubject<String>(null);
  
    private loadingDialogs: MatDialogRef<LoadingDialog>[] = [];
  
    constructor(
      private dialog: MatDialog
    ) {
    }
  
    toggleSideNav() {
      if (this.sideNavOpened$.getValue()) {
        this.sideNavOpened$.next(false);
      } else {
        this.sideNavOpened$.next(true);
      }
    }
  
    showLoading() {
      let dialogRef: MatDialogRef<LoadingDialog> = this.dialog.open(LoadingDialog, {
        panelClass: 'transparent',
        disableClose: true
      });
      this.loadingDialogs.push(dialogRef);
    }
  
    closeLoading() {
      this.loadingDialogs.forEach(dialogRef => {
        dialogRef.close();
      });
      this.loadingDialogs = [];
    }
  
  }
  