import { Component } from '@angular/core';
import { environment } from '@env/environment';
import { TranslateService } from '@ngx-translate/core';
// import { AuthenticationService } from '@app/services/authentication.service';
// import { CommonService } from '@app/services/common.service';
import { Router, ActivatedRoute } from '@angular/router';
import swal from 'sweetalert2';
import { LayoutService } from '@app/core/services/front-end-service/layout.service';
import { Locale } from '@app/core/types/locale';
import { SessionService } from '@app/core/services/front-end-service/session.service';
// import { AuthService } from 'angularx-social-login';

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']

})

export class HeaderComponent {


    constructor(
        public translate: TranslateService,
        private layoutService: LayoutService,
        private activatedRoute: ActivatedRoute,
        public sessionService: SessionService,
        private router: Router,

        // public commonService: CommonService,
        // public authenticationService: AuthenticationService,
        // private authService: AuthService,

    ) {
    }

    ngOnInit(): void {

    }


    toggleSideNav() {
        if (this.layoutService.sideNavOpened$.getValue()) {
            this.layoutService.sideNavOpened$.next(false);
        } else {
            this.layoutService.sideNavOpened$.next(true);
        }
    }
    changeLang(lang: Locale) {
        this.layoutService.showLoading();
        setTimeout(() => {
            this.sessionService.currentLang$.next(lang);
            this.layoutService.closeLoading();
        }, 200);
    }

    isActive(route: string): boolean {
        if (this.activatedRoute.snapshot.firstChild != null) {
            let url = this.activatedRoute.snapshot.firstChild.routeConfig.path;
            if (route) {
                if (`/${url}` == route) {
                    return true;
                }
                if (`/${url}`.indexOf(`${route}/`) === 0) {
                    return true;
                }
                if (`/${url}`.indexOf(`${route}$`) === 0) {
                    return true;
                }
            }
        }
        return false;
    }

    toDoctorPortal() {
        window.open("https://doctor.caseconnect.com.hk")
    }

    toMemberLogin() {
        this.router.navigate(["/sign-in"]);
    }


    logout() {
        
    }

}
