import { Injectable, Inject, PLATFORM_ID } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { NgxPermissionsService } from 'ngx-permissions';
import { Locale } from '@app/core/types/locale';
import { isPlatformBrowser, isPlatformServer } from '@angular/common';
import { CookieService } from 'ngx-cookie-service';

@Injectable({
    providedIn: 'root'
})
export class SessionService {

    public currentLang$ = new BehaviorSubject<Locale>(null);

    constructor(
        @Inject(PLATFORM_ID) private platformId: Object,
        private cookieService: CookieService,
        private permissionsService: NgxPermissionsService,
    ) {

        this.currentLang$.subscribe(lang => {
            if (lang) {
                // if (isPlatformBrowser(this.platformId)) {
                // localStorage.lang = lang;
                cookieService.set('lang', lang)
                // }
            }
        });

        if (cookieService.get('lang')) {
            this.currentLang$.next(cookieService.get('lang') as Locale);
        } else {
            this.currentLang$.next("en");
        }

        // else if (isPlatformServer(this.platformId)) {
        //     this.currentLang$.next("en");
        // }


    }

    public clearSession() {

        if (isPlatformBrowser(this.platformId)) {
            sessionStorage.clear();
        }
    }

}
