import { Meta, Title } from '@angular/platform-browser';
import { TranslateService } from '@ngx-translate/core';
import { MatSidenav } from '@angular/material/sidenav';
import { NavItem } from './core/models/front-end/nav-item';
import { LayoutService } from './core/services/front-end-service/layout.service';
import { SessionService } from './core/services/front-end-service/session.service';
import { isPlatformBrowser, DOCUMENT } from '@angular/common';
import { ActivatedRouteSnapshot, ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { distinctUntilChanged } from 'rxjs/operators';
import { Locale } from '@app/core/types/locale';

declare let gtag: Function;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'client-web';

  //@ViewChild(MatSidenav, { static: true }) sideNav: MatSidenav;

  navItems: NavItem[] = [
    // {
    //   displayName: 'DevFestFL',
    //   iconName: 'recent_actors',
    //   route: 'devfestfl',
    //   children: [
    //     {
    //       displayName: 'Speakers',
    //       iconName: 'group',
    //       route: 'devfestfl/speakers',
    //       children: [
    //         {
    //           displayName: 'Michael Prentice',
    //           iconName: 'person',
    //           route: 'devfestfl/speakers/michael-prentice',
    //           children: [
    //             {
    //               displayName: 'Create Enterprise UIs',
    //               iconName: 'star_rate',
    //               route: 'devfestfl/speakers/michael-prentice/material-design'
    //             }
    //           ]
    //         },
    //       ]
    //     },
    //   ]
    // },
    {
      displayName: "HEADER.HOME",
      route: '/'
    },
    {
      displayName: "HEADER.ABOUT_US",
      // iconName: 'videocam',
      route: '/about-us'
    },
    {
      displayName: "HEADER.OUR_SERVICES",
      // iconName: 'videocam',
      route: '/our-services'
    },
    {
      displayName: "HEADER.JOIN_OUR_NETWORK",
      // iconName: 'videocam',
      route: '/join-our-network'
    },
    // {
    //   displayName: "HEADER.FIND_DOCTORS",
    //   iconName: 'videocam',
    //   route: '/booking/find-doctor'
    // },
    // {
    //   displayName: "HEADER.MY_ACCOUNT",
    //   iconName: 'videocam',
    //   route: '/my-account'
    // },
    {
      displayName: "HEADER.DOCTOR_SING_IN",
      // iconName: 'videocam',
      externalLink: "https://doctor.caseconnect.com.hk"
    },
    {
      displayName: "HEADER.MEMBER_SIGN_IN",
      iconName: 'videocam',
      route: '/sign-in'
    },
  ];

  constructor(
    @Inject(PLATFORM_ID) private platformId: Object,
    @Inject(DOCUMENT) private document: Document,
    private meta: Meta,
    private router: Router,
    private renderer: Renderer2,
    public translate: TranslateService,
    private layoutService: LayoutService,
    private sessionService: SessionService,
    private activatedRoute: ActivatedRoute,
    private titleService : Title,
  ) { }


  ngOnInit(): void {

    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
        return;
      }
      if (isPlatformBrowser(this.platformId)) {
        //document.querySelector('.mat-sidenav-content').scrollTop = 0;
        this.layoutService.sideNavOpened$.next(false);
      }
    });

    this.router.events
      .pipe(distinctUntilChanged((previous: any, current: any) => {
        if (current instanceof NavigationEnd) {
          return previous.url === current.url;
        }
        return true;
      }))
      .subscribe((x: any) => {
        if (isPlatformBrowser(this.platformId)) {
          gtag('event', 'page_view', { 'page_path': x.url });
        }
      });

    // [en, zh-Hant].forEach(locale => {
    //   this.translate.setTranslation(locale.lang, locale.data, true);
    //   this.translate.addLangs([locale.lang]);
    // });

    this.translate.use(this.sessionService.currentLang$.getValue());

    this.sessionService.currentLang$.subscribe(lang => {
      this.translate.use(lang);
      if (lang == 'en') {
        this.document.documentElement.lang = 'en';
        this.titleService.setTitle('EC Healthcare | Member Registration ')
      } else if (lang == 'zh-Hant') {
        this.document.documentElement.lang = 'zh-Hant';
        this.titleService.setTitle('醫思健康 | 會員登記表格')
      } else if (lang == 'zh-Hans') {
        this.document.documentElement.lang = 'zh-Hans';
        this.titleService.setTitle('医思健康 | 会员登记表格')
      }
    });

    if (isPlatformBrowser(this.platformId)) {
      this.layoutService.sideNavOpened$.subscribe(opened => {
        if (opened) {
          //this.sideNav.open();
          this.renderer.addClass(document.body, 'overflow-hidden');
          this.renderer.removeClass(document.body, 'overflow-auto');
        } else {
          //this.sideNav.close();
          this.renderer.addClass(document.body, 'overflow-auto');
          this.renderer.removeClass(document.body, 'overflow-hidden');
        }
      });
    }

    this.activatedRoute.queryParams.subscribe(params => {
      if (params['lang']) {
        let lang = params['lang'];
        if (['en', 'zh-Hant', 'zh-Hans'].includes(lang)) {
          this.sessionService.currentLang$.next(lang);
        }
      }
    });

    // this.meta.updateTag(
    //   {
    //     name: 'description',
    //     content: 'CASE CONNECT is committed to connecting medical and healthcare practitioners to provide quality services to the customers in Hong Kong and China.'
    //   }
    // )

  }

  changeLang(lang: Locale) {
    this.layoutService.showLoading();
    setTimeout(() => {
      this.sessionService.currentLang$.next(lang);
      this.layoutService.closeLoading();
    }, 200);
  }
  onSideNavToggle(opened: boolean) {
    this.layoutService.sideNavOpened$.next(opened);
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (event.target.innerWidth >= 992) {
      this.layoutService.sideNavOpened$.next(false);
    }
  }

  // onActivate(event) {
  //   let scrollToTop = window.setInterval(() => {
  //     let pos = window.pageYOffset;
  //     if (pos > 0) {
  //       window.scrollTo(0, pos - 20); // how far to scroll on each step
  //     } else {
  //       window.clearInterval(scrollToTop);
  //     }
  //   }, 8);
  // }
}
